/* layer */
.dimmed_layer{display:none;position:fixed;left:0;right:0;top:0;bottom:0;z-index:1000;background-color:#000;opacity:0.8;filter:alpha(opacity=80)
}
.popup_layer{display:none;position:fixed;top:50%;left:50%;z-index:1010;
  -webkit-transform:translate3d(-50%,-50%,0);-moz-transform:translate3d(-50%,-50%,0);-ms-transform:translate(-50%,-50%);transform:translate3d(-50%,-50%,0)
}
.inner_popup_layer{position:relative;width:826px;background-color:#fff;color:#333
}
.inner_popup_layer .layer_head{padding:57px 55px 0
}
.inner_popup_layer .layer_body{padding:17px 55px 56px
}
.inner_popup_layer .tit_popup{display:block;font-size:24px;line-height:28px;color:#3e4039;letter-spacing:-0.075em
}
.inner_popup_layer .btn_close{position:absolute;top:20px;right:20px;padding:10px;-webkit-transition:all 0.25s ease;-moz-transition:all 0.25s ease;transition:all 0.25s ease
}
.inner_popup_layer .ico_close{display:inline-block;overflow:hidden;width:28px;height:28px;font-size:0;line-height:0;background:url(https://t1.kakaocdn.net/gamepub/img-contents/event/promotion/181129_battle/img_comm.png) no-repeat -70px -160px;vertical-align:top;text-indent:-9999px
}

/* layer 1 */
.inner_type1_layer{opacity:0;-webkit-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);-moz-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
}
.inner_type1_layer .btn_close:hover{-webkit-transform-origin:center center;-moz-transform-origin:center center;-ms-transform-origin:center center;transform-origin:center center;
-webkit-transform:rotate(90deg);-moz-transform:rotate(90deg);-ms-transform:rotate(90deg);transform:rotate(90deg)
}
.on .inner_type1_layer{opacity:1
}

/* layer 2 */
.inner_type2_layer{opacity:0;
  -webkit-transform:translateY(60px);-moz-transform:translateY(60px);-ms-transform:translateY(60px);transform:translateY(60px);
  -webkit-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);-moz-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
}
.inner_type2_layer .btn_close:hover{-webkit-transform:translateY(-10px);-moz-transform:translateY(-10px);-ms-transform:translateY(-10px);transform:translateY(-10px)
}
.on .inner_type2_layer{opacity:1;-webkit-transform:translateY(0);-moz-transform:translateY(0);-ms-transform:translateY(0);transform:translateY(0)
}

/* layer 3 */
.inner_type3_layer{opacity:0;
  -webkit-transform:translateY(-60px);-moz-transform:translateY(-60px);-ms-transform:translateY(-60px);transform:translateY(-60px);
  -webkit-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);-moz-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
}
.inner_type3_layer .btn_close:hover{-webkit-transform:scale(1.2);-moz-transform:scale(1.2);-ms-transform:scale(1.2);transform:scale(1.2)
}
.on .inner_type3_layer{opacity:1;-webkit-transform:translateY(0);-moz-transform:translateY(0);-ms-transform:translateY(0);transform:translateY(0)
}

/* layer 4 */
.type4_layer{width:0;opacity:0;
  -webkit-transform:translate3d(-50%, -50%, 0);-moz-transform:translate3d(-50%, -50%, 0);-ms-transform:translate(-50%, -50%);transform:translate3d(-50%, -50%, 0);
  -webkit-transform-origin:center center;-moz-transform-origin:center center;-ms-transform-origin:center center;transform-origin:center center;
  -webkit-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);-moz-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94)
}
.on.type4_layer{width:100%;opacity:1
}
.inner_type4_layer{width:100%;height:337px;background:#1c1c1c url(https://t1.kakaocdn.net/gamepub/img-contents/event/promotion/181127_battle/img_distance.png) no-repeat 50% 50%
}
.inner_type4_layer .layer_head,
.inner_type4_layer .layer_body{overflow:hidden;font-size:0;line-height:0;text-indent:-9999px
}
.inner_type4_layer .btn_close{color:#fff
}

/* layer 5 */
.inner_type5_layer{opacity:0;-webkit-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);-moz-transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);transition:all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
}
.on .inner_type5_layer{opacity:1
}


.btn_type_comm{display:inline-block;width:168px;height:55px;font-size:16px;line-height:55px;border-radius:3px;background-color:#efbc15;vertical-align:top;color:#fff
}
.btn_type_comm:hover{text-decoration:none
}
.btn_type1{overflow:hidden;position:relative
}
.btn_type1 .type1_effect{position:absolute;top:0;right:0;bottom:0;left:0;-webkit-transition:0.4s cubic-bezier(.48,0,0,1);-moz-transition:0.4s cubic-bezier(.48,0,0,1);-o-transition:0.4s cubic-bezier(.48,0,0,1);transition:0.4s cubic-bezier(.48,0,0,1)
}
.btn_type1 .type1_effect:after{position:absolute;top:0;right:0;bottom:0;left:0;background-image:linear-gradient(120deg, transparent 15%, hsla(39, 78%, 89%, 0.15) 50%, transparent 85%);opacity:1;content:"";-webkit-transform:translate3d(-100%, 0, 0);-moz-transform:translate3d(-100%, 0, 0);-ms-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0);-webkit-transition:transform 0.4s linear;-moz-transition:transform 0.4s linear;-o-transition:transform 0.4s linear;transition:transform 0.4s linear
}
.btn_type1:hover .type1_effect:after{-webkit-transform:translate3d(100%, 0, 0);-moz-transform:translate3d(100%, 0, 0);-ms-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}
.btn_type2{-webkit-transition:transform 0.4s linear;-moz-transition:transform 0.4s linear;-o-transition:transform 0.4s linear;transition:transform 0.3s ease
}
.btn_type2:hover{-webkit-transform:scale(1.1);-moz-transform:scale(1.1);-ms-transform:scale(1.1);transform:scale(1.05)
}
.btn_type3{-webkit-transition:background-color 0.4s linear;-moz-transition:background-color 0.4s linear;-o-transition:background-color 0.4s linear;transition:background-color 0.3s ease
}
.btn_type3:hover{background-color:#d8a80b
}
.btn_type4{overflow:hidden;position:relative;-webkit-transition:background-color 0.4s linear;-moz-transition:background-color 0.4s linear;-o-transition:background-color 0.4s linear;transition:background-color 0.3s ease
}
.btn_type4 .type4_effect{position:absolute;bottom:0;left:0;right:0;height:0;background-color:#d8a80b;-webkit-transition:height 0.4s linear;-moz-transition:height 0.4s linear;-o-transition:height 0.4s linear;transition:height 0.3s ease
}
.btn_type4 .txt_btn{position:relative
}
.btn_type4:hover{background-color:#d8a80b
}
.btn_type4:hover .type4_effect{height:100%
}
.btn_type5{-webkit-transition:transform 0.4s linear;-moz-transition:transform 0.4s linear;-o-transition:transform 0.4s linear;transition:transform 0.4s ease
}
.btn_type5:hover{-webkit-transform:translateY(-10px);-moz-transform:translateY(-10px);-ms-transform:translateY(-10px);transform:translateY(-8px)
}
.btn_type6{position:relative;-webkit-transition:background-color 0.4s ease;-moz-transition:background-color 0.4s ease;-o-transition:background-color 0.4s ease;transition:background-color 0.4s ease
}
.btn_type6:hover{background-color:#d8a80b
}
.btn_type6 .arrow_type6{position:absolute;top:0;right:20px;opacity:0;-webkit-animation-play-state:paused;-moz-animation-play-state:paused;animation-play-state:paused;
  -webkit-transition:opacity 0.4s linear;-moz-transition:opacity 0.4s linear;-o-transition:opacity 0.4s linear;transition:opacity 0.4s ease
}
.btn_type6:hover .arrow_type6{opacity:1;-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0);
  -webkit-transform-origin:left top;-moz-transform-origin:left top;-ms-transform-origin:left top;transform-origin:left top;
  -webkit-animation-name:anitype6;-moz-animation-name:anitype6;animation-name:anitype6;
  -webkit-animation-duration:3s;-moz-animation-duration:3s;animation-duration:3s;
  -webkit-animation-play-state:running;-moz-animation-play-state:running;animation-play-state:running
}

/* animation */
@-webkit-keyframes anitype6 {
0%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
16%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
32%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
48%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
64%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
80%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
100%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
}
@-moz-keyframes anitype6 {
0%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
16%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
32%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
48%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
64%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
80%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
100%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
}
@keyframes anitype6 {
0%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
16%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
32%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
48%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
64%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
80%{-webkit-transform:translateX(6px);-moz-transform:translateX(6px);-ms-transform:translateX(6px);transform:translateX(6px)
}
100%{-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);transform:translateX(0)
}
}

/* font-face */

/* reset */
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,button{margin:0;padding:0}
fieldset,img{border:0 none}
dl,ul,ol,menu,li{list-style:none}
blockquote, q{quotes: none}
blockquote:before, blockquote:after,q:before, q:after{content:'';content:none}
input,select,textarea,button{vertical-align:middle}
input::-ms-clear{display:none}
button{border:0 none;background-color:transparent;cursor:pointer}
body{background:#fff}
body,th,td,input,select,textarea,button{font-size:14px;line-height:1.5;font-family:'Noto Sans KR', AppleSDGothicNeo-Regular,'Malgun Gothic','\B9D1\C740   \ACE0\B515',dotum,'\B3CB\C6C0',sans-serif;color:#212220}
a{color:#333;text-decoration:none}
a:active, a:hover{text-decoration:underline}
a:active{background-color:transparent}
address,caption,cite,code,dfn,em,var{font-style:normal;font-weight:normal}

.ir_pm{display:block;overflow:hidden;font-size:0;line-height:0;text-indent:-9999px}
.ir_wa{display:block;overflow:hidden;position:relative;z-index:-1;width:100%;height:100%}
.screen_out{overflow:hidden;position:absolute;width:0;height:0;line-height:0;text-indent:-9999px}
.hide{display:none}
.img_thumb{width:100%;vertical-align:top}

.link_code{display:inline-block;margin:0 6px 0 10px;padding:0 10px;font-weight:400;font-size:12px;line-height:22px;border-radius:3px;background-color:#333;color:#fff;vertical-align:middle}
.link_code:hover{background-color:#000;text-decoration:none}

/* list */
.list_visual{margin-top:60px;clear:both}
.list_visual li{float:left;position:relative;width:545px;margin:0 0 65px 0}
.list_visual .num_visual{position:absolute;top:-37px;left:-14px;font-weight:bold;font-size:23px;color:#000;letter-spacing:0.02em;text-shadow: 2px 2px 0px rgba(255, 255, 255, 0.8);}
.list_visual .area_thumb{overflow:hidden;float:left;width:250px;border:1px solid #c7c7c7;border-radius:10px;}
.list_visual .area_info{overflow:hidden;padding:0 20px;}
.list_visual .tit_visu{display:block;overflow:hidden;font-weight:600;font-size:17px;text-overflow:ellipsis;white-space:nowrap;letter-spacing:-0.035em}
.list_visual .tag_game{display:inline-block;margin:-2px 6px 0 0;padding:4px 10px;font-weight:400;font-size:12px;border-radius:3px;background-color:#e4b00e;color:#fff;vertical-align:middle}
.list_visual .tag_skill{display:inline-block;margin-right:6px;vertical-align:top;}
.list_visual .txt_visu{height:38px;margin-top:10px;font-size:13px;color:#777}
.list_visual .link_go{display:block;width:140px;height:47px;font-weight:bold;font-size:16px;line-height:47px;border:2px solid #000;border-radius:6px;text-decoration:none;text-align:center}
.list_visual .link_go:hover{background-color:#000;color:#fff}
.list_visual .txt_browser{font-weight:600;color:#e4b00e}
.list_visual .txt_date{font-weight:600;color:#777}
.list_visual .group_btn{margin-top:10px}

.list_particle{overflow:hidden}
.list_particle li{float:left;width:300px;margin:0 25px 35px 0}
.list_particle .num_visual{position:absolute;top:8px;left:12px;font-weight:bold;font-size:23px;color:#fff;letter-spacing:0.02em}
.list_particle .link_particle{display:block}
.list_particle .area_thumb{display:block;overflow:hidden;position:relative;border:1px solid #cecece;border-radius:10px;box-sizing:border-box}
.list_particle .area_info{display:block;overflow:hidden;padding:12px 0;line-height:24px}
.list_particle .tit_particle{float:left;font-size:18px}
.list_particle .txt_more{float:right;font-size:13px;color:#777}
.list_particle .txt_type{position:absolute;right:8px;bottom:7px;padding:4px 10px;font-weight:500;font-size:12px;border-radius:3px 3px 8px 3px;background-color:#a2a1a1;color:#fff;letter-spacing:0.025em}
.list_particle .img_thumb{height:180px}

.list_info{overflow:hidden;margin-top:22px}
.list_info dt{display:none}
.list_info dd{float:left;padding-right:15px}

.list_pages li{overflow:hidden;max-width:800px;line-height:28px}
.list_pages li + li{margin-top:28px}
.list_pages [class*="link_pages"]{display:inline-block;overflow:hidden;line-height:28px;text-decoration:none;vertical-align:middle}
.list_pages .tit_pages{float:left;min-width:50px;padding-right:10px;font-weight:bold;font-size:22px;}
.list_pages .txt_pages{float:left;overflow:hidden;font-size:16px;color:#777}
.list_pages .txt_bar{padding:0 6px;color:#b4b4b4}
.list_pages .em_layer{color:#000}
.list_pages .group_link{float:right;font-size:16px}
.list_pages .link_page{display:inline-block}
.list_pages .link_page + .link_page{margin-left:40px}

/* layout */
.skipnavi{display:none}
.wrap_games{min-width:1300px;padding-left:200px}

.head_games{position:fixed;top:0;left:0;width:200px;height:100vh;z-index:100;padding:46px 20px;border-right:1px solid #efefef;background-color:#fff;box-sizing:border-box}
.head_games .logo_games{margin-bottom:37px}
.head_games .link_logo{display:block;text-decoration:none}
.head_games .img_logo{display:block}
.head_games .txt_logo{display:block;margin-top:8px;font-size:16px;color:#ababab}

.menu_games li + li{margin-top:22px}
.menu_games .link_menu{font-weight:600;font-size:15px;letter-spacing:-0.025em;color:#000}
.menu_games .on .link_menu{color:#e4b00e}

.cont_games{overflow:hidden;min-height:100vh;padding:35px 40px;box-sizing:border-box}
.cont_games .inner_cont:after{display:block;clear:both;content:""}
.tit_cate{position:relative;margin-bottom:45px;padding-bottom:20px;font-size:36px}
.tit_cate:after{position:absolute;left:0;bottom:0;width:34px;height:5px;background-color:#000;content:""}
.tit_section{padding-bottom:12px;font-size:20px}
.txt_section{margin-bottom:42px;font-weight:bold;font-size:16px;letter-spacing:-0.025em}

.foot_games{border-top:1px solid #efefef;text-align:right;}
.foot_games .inner_foot{padding:10px 20px}
.foot_games .copy_games{color:#d6d6d6}

/* box */
.box_srch{margin:-10px 0 50px}
.box_srch .select_srch{display:inline-block;width:100px;height:38px;padding:0 10px;line-height:30px;font-size:14px;border:1px solid #000;background-color:#fff;vertical-align:top;box-sizing:border-box}
.box_srch .tf_srch{display:inline-block;width:200px;height:38px;padding:0 10px;line-height:30px;border:1px solid #000;border-radius:5px;vertical-align:top;box-sizing:border-box}

.box_btn{display:table;float:left;position:relative;width:170px;height:146px;margin:0 70px 35px 0;border-radius:10px;box-sizing:border-box}
.box_btn .inner_box{display:table-cell;padding-top:30px;text-align:center;vertical-align:middle;box-sizing:border-box}
.box_btn .tit_box{position:absolute;top:8px;left:-13px;font-weight:bold;font-size:23px;color:#000;letter-spacing:0.02em}
.box_btn .link_txt{position:absolute;top:14px;right:0;font-size:14px;color:#6e6e6e;text-decoration:underline;letter-spacing:0.02em}

/* 뷰 페이지 전환 효과 */
.fade-enter-active,.fade-leave-active{transition-duration:0.2s;transition-property:opacity}
.fade-enter, .fade-leave-active{opacity:0}
